import "core-js/modules/es.array.reduce.js";
import "core-js/modules/web.dom-collections.iterator.js";
import { showDialog, showNotify, ActionSheet, Progress } from 'vant';
import { useWindowSize } from '@vant/use';
import Navbar from '@/components/Navbar.vue';

let data = require('@/data/CN/wish.json');

let preset = {
  gachaTime: 0,
  initialAngle_: Math.PI,
  initialAngle: Math.PI,
  angles_: [Math.PI, Math.PI, Math.PI],
  angles: [Math.PI, Math.PI, Math.PI],
  frame: 0,
  maxFrame_: undefined,
  gifts: [0, 1, 2].map(i => ({
    name: data[data.length - 1].gifts[i],
    count: 1,
    remain: 1,
    had: false
  })),
  itemsGot: []
};

let easeInOut = (t, b, c, d) => {
  if ((t /= d / 2) < 1) return c / 2 * t * t + b;
  return -c / 2 * (--t * (t - 2) - 1) + b;
}; // 网上搜的


export default {
  name: 'WishSimulator',
  components: {
    [ActionSheet.name]: ActionSheet,
    [Progress.name]: Progress,
    Navbar
  },

  data() {
    const {
      width
    } = useWindowSize();
    return { ...preset,
      data,
      rewards: data[data.length - 1].rewards.map(v => ({ ...v,
        name: this.$t(`items.${v.id}`)
      })),
      active: 0,
      volume: data.length - 1,
      volumes: data.map((v, i) => ({
        name: v.name,
        index: i
      })),
      width: Math.min(width.value, 425),
      maxFrames: [216, 144, 72, 36, 1],
      volumesShow: false,
      itemsGotShow: false,
      possibilityShow: false,
      dialogTheme: {
        dialogFontSize: 'var(--van-font-size-md)'
      }
    };
  },

  watch: {
    volume(v) {
      Object.assign(this, { ...preset,
        gifts: [0, 1, 2].map(i => ({
          name: data[v].gifts[i],
          count: 1,
          remain: 1,
          had: false
        })),
        rewards: data[v].rewards.map(v => ({ ...v,
          name: this.$t(`items.${v.id}`)
        }))
      });
      this.updateCanvas();
    }

  },
  computed: {
    totalWeight() {
      return this.rewards.reduce((prev, curr) => {
        if (curr.remain !== 0) return prev + curr.weight;
        return prev;
      }, 0);
    },

    possibilities() {
      return this.rewards.map(item => item.remain === 0 ? 0 : item.weight / this.totalWeight);
    },

    possibilitiesString() {
      return this.possibilities.map(p => Math.round(p * 10000) / 100 + '%');
    },

    giftPossibilityString() {
      return Math.round(Math.pow(this.possibilities[0], 3) * 10000) / 100 + '%';
    },

    remainGifts() {
      return this.gifts.reduce((prev, curr) => prev + curr.remain, 0);
    },

    shapeAngle() {
      return this.possibilities.map(p => 2 * Math.PI * p);
    },

    beginAngle() {
      let result = [0];

      for (let i in this.shapeAngle) result.push(result[i] + this.shapeAngle[i]);

      return result;
    },

    maxFrame: {
      get() {
        if (this.maxFrame_ === undefined) {
          var _localStorage;

          let data = (_localStorage = localStorage) === null || _localStorage === void 0 ? void 0 : _localStorage.getItem('WishSimMaxFrame');
          return data !== null ? Number(data) : 144;
        }

        return this.maxFrame_;
      },

      set(v) {
        var _localStorage2;

        this.maxFrame_ = v;
        (_localStorage2 = localStorage) === null || _localStorage2 === void 0 ? void 0 : _localStorage2.setItem('WishSimMaxFrame', v);
      }

    }
  },

  mounted() {
    let canvas = document.getElementById('canvas');
    let ctx = canvas.getContext('2d');

    let getPixelRatio = context => {
      let backingStore = context.backingStorePixelRatio || context.webkitBackingStorePixelRatio || context.mozBackingStorePixelRatio || context.msBackingStorePixelRatio || context.oBackingStorePixelRatio || context.backingStorePixelRatio || 1;
      return (window.devicePixelRatio || 1) / backingStore;
    }; // 网上搜的


    let ratio = getPixelRatio(ctx);
    canvas.style.width = canvas.width + 'px';
    canvas.style.height = canvas.height + 'px';
    canvas.width = canvas.width * ratio;
    canvas.height = canvas.height * ratio;
    ctx.scale(ratio, ratio);
    this.updateCanvas(this.angles_);
  },

  methods: {
    showWeightInfo() {
      showDialog({ ...this.$root.dialogSettings,
        message: this.$t('hint.wishsim')
      });
    },

    selectVolume(v) {
      this.volume = v.index;
    },

    setMaxFrame(v) {
      this.maxFrame = v;
      this.frame = 0;
    },

    gacha3Times() {
      if (this.frame && this.frame <= this.maxFrame) return;
      if (this.gachaTime > data[this.volume].must[2]) return;
      if (this.remainGifts == 0) return;
      this.gachaTime += 1;
      this.initialAngle_ = this.initialAngle;
      this.angles_ = this.angles;
      this.initialAngle = 2 * Math.PI * Math.random();
      let result = {};
      let angles = [];
      let isGift = true;

      for (let i = 0; i < 3; i++) {
        let reward = this.gacha1Time();
        if (reward != 0) isGift = false;
        if (!result[reward]) result[reward] = { ...this.rewards[reward]
        };else result[reward].count += this.rewards[reward].count;
        angles.push(this.initialAngle + this.beginAngle[reward] + this.shapeAngle[reward] * Math.random());
      }

      this.frame = 0;
      this.angles = angles;

      let animate = () => {
        this.updateCanvas();
        this.frame += 1;
        if (this.frame <= this.maxFrame) requestAnimationFrame(animate);else {
          if (isGift) showNotify({
            type: 'success',
            message: this.$t('wishsim.notify.success', [data[this.volume].gift])
          });

          if (!isGift) {
            for (let i of [0, 1, 2]) {
              if (this.remainGifts == 3 - i && this.gachaTime == data[this.volume].must[i]) {
                isGift = true;
                showNotify({
                  type: 'success',
                  message: this.$t('wishsim.notify.protect', [data[this.volume].must[i], data[this.volume].gift])
                });
                break;
              }
            }
          }

          if (isGift) {
            let random = Math.random();

            for (let i in this.gifts) {
              let item = this.gifts[i];
              if (item.remain == 0) continue;

              if (random <= 1 / this.remainGifts) {
                item.remain -= 1;
                item.had = true;
                item.time = this.gachaTime;
                result['gift'] = item;
                break;
              }

              random -= 1 / this.remainGifts;
              this.updateCanvas();
            }
          }

          this.itemsGot = result;
          this.itemsGotShow = true;
        }
      };

      animate();
      return result;
    },

    gacha1Time() {
      let random = Math.random();

      for (let [index, item] of this.rewards.entries()) {
        if (item.remain === 0) continue;

        if (random <= this.possibilities[index]) {
          if (item.remain > 0) item.remain -= 1;
          return index;
        }

        random -= this.possibilities[index];
      }
    },

    updateCanvas() {
      let width = this.width;
      let canvas = document.getElementById('canvas');
      let ctx = canvas.getContext('2d');
      let centerX = width * 0.5;
      let centerY = width * 0.5;
      let radius = width * 0.3;
      let textRadius = width * 0.375;
      let ballRadius = width * 0.015;
      let ballPositionRadius = [width * 0.1, width * 0.17, width * 0.24];
      let initialAngle = easeInOut(this.frame, this.initialAngle_, this.initialAngle - this.initialAngle_ - 10 * Math.PI, this.maxFrame);
      let angles = this.angles.map((angle, i) => easeInOut(this.frame, this.angles_[i], angle - this.angles_[i] + (6 + 4 * i) * Math.PI, this.maxFrame));
      ctx.clearRect(0, 0, width, width);
      ctx.textAlign = 'center';

      for (let i in this.rewards) {
        if (this.rewards[i].remain === 0) continue;
        ctx.beginPath();
        ctx.moveTo(centerX, centerY);
        ctx.arc(centerX, centerY, radius, initialAngle + this.beginAngle[i], initialAngle + this.beginAngle[i] + this.shapeAngle[i]);
        ctx.closePath();
        if (i == 0) ctx.fillStyle = '#525997';else ctx.fillStyle = '#2a2c42';
        ctx.strokeStyle = '#b9bcc2';
        ctx.setLineDash([1, 10]);
        ctx.fill();
        ctx.stroke();
        let x = centerX + textRadius * Math.cos(initialAngle + this.beginAngle[i] + this.shapeAngle[i] / 2);
        let y = centerY + textRadius * Math.sin(initialAngle + this.beginAngle[i] + this.shapeAngle[i] / 2);
        ctx.fillText(this.rewards[i].name, x, y);
        if (this.rewards[i].remain) ctx.fillText(`× ${this.rewards[i].count} × ${this.rewards[i].remain}`, x, y + 12);else ctx.fillText(`× ${this.rewards[i].count} × ∞`, x, y + 12);
      }

      for (let i = 0; i < 3; i++) {
        ctx.beginPath();
        if (i == 1) ctx.setLineDash([10, 10]);else ctx.setLineDash([]);
        ctx.arc(centerX, centerY, ballPositionRadius[i], 0, 2 * Math.PI);
        ctx.closePath();
        ctx.stroke();
      }

      ctx.strokeStyle = '#fff';
      ctx.fillStyle = '#e5ce9c';

      for (let i in angles) {
        ctx.beginPath();
        let x = centerX + ballPositionRadius[i] * Math.cos(angles[i]);
        let y = centerY + ballPositionRadius[i] * Math.sin(angles[i]);
        ctx.arc(x, y, ballRadius, 0, 2 * Math.PI);
        ctx.closePath();
        ctx.fill();
        ctx.stroke();
      }

      if (this.remainGifts == 0) {
        ctx.fillStyle = '#fff';
        ctx.fillText(this.$t('wishsim.complete', [this.gachaTime, data[this.volume].gift]), centerX, centerY);
      }
    }

  }
};